import styled from 'styled-components';

const Subheading = styled.h3`
  text-transform: uppercase;
  font-family: 'Montserrat', sans-serif;
  font-style: italic;
  border-bottom: 4px solid rgb(216, 216, 216);
  padding: 0 7px 10px;
  margin-top: 0;
`;

export default Subheading;



